
@font-face{
  font-family: headFont;
  src: url(supermario.ttf);

}


*{
  font-family: 'sans-serif';
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size:62.4%;
}
body{
  
  height: 100%;
  background: linear-gradient(rgb(10, 83, 80), #0e1672);

}


.center-content{
    
  display: flex;
  flex-direction: column;
   margin: 10px auto;
   width: 100%;
   text-align: center;
   
 
}

.card{
  
  background-color: lightseagreen ;
  transition: all 1s ease-in-out;
  margin: 10px;
  font-size: 7rem;
}


.card:hover{
   transform: scale(1.1);

}

.flex-box{
  display: flex;
   flex-wrap: wrap;
   flex-shrink: 0;
   transition: all 1s ease-in-out;
   flex-grow: 1;
   justify-content: center;
}


.center-content h1{
  margin: 20px 0px;
  font-family: 'headFont' ;
  letter-spacing: 3px;
  font-size: 7rem;
  color: white;
}

.searchBox{
  padding: 10px;
}
.searchBox input{
  padding: 10px;
  border: none;
}

.scroll{
  border:2px soild black;
  overflow-y: scroll;
  height: 500px;

}